import css from './Blog.module.scss'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import BlogTemplate from 'components/Templates/BlogTemplate/BlogTemplate'
import { dataBlog } from 'utils/dataBlog'
import { useCallback, useMemo } from 'react'
import PostPreview from 'components/Molecules/PostPreview/PostPreview'
import { useSelector } from 'react-redux'
import { BLOG_POST_CREATE } from 'utils/routes'
import { isMobile } from 'react-device-detect'
import BlogDataProvider from 'components/Organisms/BlogDataProvider/BlogDataProvider'
import PreloaderManager from 'components/Organisms/PreloaderManager/PreloaderManager'
import { LoadingStatuses } from 'utils/const'

function Blog () {
  const isAuth = useSelector(store => store.auth.email)
  
  const renderItems = useCallback(data => {
    if (!data) return null
    
    return data.map((item, index) => (
      <PostPreview
        isOdd={index % 2 !== 0}
        key={index}
        {...item}
      />
    ))
  }, [])
  
  return (
    <section className={css.section}>
      <Container>
        <div className={css.header}>
          <Heading
            isCapitalized
          >
            Блог
          </Heading>
          { isAuth && !isMobile &&
            <Button
              link={BLOG_POST_CREATE}
              variant={ ButtonVariants.ARROW }
            >
              Создать запись
            </Button>
          }
        </div>
        <BlogDataProvider
          renderChildren={({posts, fetchingStatus}) => (
            <PreloaderManager
              isDataExists={Boolean(posts)}
              isLoading={fetchingStatus === LoadingStatuses.LOADING}
              isLoaded={fetchingStatus === LoadingStatuses.LOADED}
              noDataMessage={`В базе данных нет постов для блога.`}
            >
              <BlogTemplate className={css.list}>
                { renderItems(posts) }
              </BlogTemplate>
              {/*<Button*/}
              {/*  onClick={() => alert(`Здесь будет подгрузка карточек с бэка`)}*/}
              {/*  variant={ButtonVariants.MORE}*/}
              {/*  className={css.button}*/}
              {/*>*/}
              {/*  Загрузить еще*/}
              {/*</Button>*/}
            </PreloaderManager>
          )}
        />
      </Container>
    </section>
  )
}

export default Blog
