import { collection, getDocs, getDoc, doc, addDoc } from 'firebase/firestore'
import { getDownloadURL, getStorage, ref, uploadBytes } from 'firebase/storage'

export function fetchPosts (db) {
  const blogRef = collection(db, `blog`)
  return getDocs(blogRef)
    .then(snapshot => {
      const data = []
      snapshot.forEach(doc => {
        const post = doc.data()
        
        const imageRequest = post?.image ? fetchImageURL({
          postID: doc.id,
          fileName: post.image
        })
          .then(url => {
            return {
              ...post,
              id: doc.id,
              image: url,
              createdAt: post.createdAt?.seconds
                ? post.createdAt.seconds * 1000
                : post.createdAt
            }
          }) : new Promise(resolve => resolve(post))
        
        data.push(imageRequest)
      })
      
      return Promise.all(data)
    })
}

export function fetchSpecificPost ({postID, db}) {
  const postRef = doc(db, `/blog/${postID}`)
  
  return getDoc(postRef)
    .then(snapshot => {
      return snapshot.data()
    })
    .then(postData => {
      return fetchImageURL({postID: postData.id, fileName: postData.image})
        .then(url => ({
          ...postData,
          createdAt: postData.createdAt?.seconds
            ? postData.createdAt.seconds
            : postData.createdAt,
          image: url
        }))
    })
}

export function fetchImageURL ({postID, fileName}) {
  const storage = getStorage()
  const imageRef = ref(storage, `blog/${postID}/${fileName}`)
  
  return getDownloadURL(imageRef)
    .catch(() => {
      console.log(`smth wrong`)
    })
}

export function saveImage ({postID, image}) {
  const storage = getStorage()
  const storageRef = ref(storage, `blog/${postID}/${image.name}`)
  
  return uploadBytes(storageRef, image)
}

export function savePost ({db, data}) {
  const collectionRef = collection(db, `blog`)
  
  return addDoc(collectionRef, data)
    .then(response => {
      return response.id
    })
}