import { memo } from 'react'
import css from './PostPreview.module.scss'
import classnames from 'classnames'
import Link from 'components/Atoms/Link/Link'
import { BLOG } from 'utils/routes'
import { getDateComponents } from 'utils'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import ImageController from 'components/Organisms/ImageController/ImageController'

function PostPreview ({
  className,
  TagName = `li`,
  id,
  createdAt,
  heading,
  preview,
  content,
  image,
  isOdd
}) {
  const {
    year,
    month,
    day
  } = getDateComponents(createdAt)
  
  return (
    <TagName
      className={classnames(className, {
        [css.wrapperOdd]: isOdd
      })}
    >
      <Link
        link={{
          pathname: `${BLOG}/${id}`,
          state: {
            data: {
              id,
              createdAt,
              heading,
              image,
              content
            }
          }
      }}
        className={css.link}
      >
        <div className={css.frame}>
          <img
            src={image}
            className={css.image}
            alt={heading}
          />
        </div>
        <div className={css.content}>
          <span className={css.date}>
            { `${day}.${month}.${`${year}`.slice(2)}` }
          </span>
          <Heading
            className={css.heading}
            tag={HeadingTypes.H3}
          >
            { heading }
          </Heading>
          <p
            className={css.description}
            dangerouslySetInnerHTML={{ __html: preview }}
          />
        </div>
      </Link>
    </TagName>
  )
}

export default memo(PostPreview)