import React, { useEffect, useState } from 'react'
import NoDataMessage from 'components/Atoms/NoDataMessage/NoDataMessage'
import Spinner from 'components/Atoms/Spinner/Spinner'

const PreloaderManager = ({
  isDataExists,
  isLoading,
  isLoaded,
  currentPage,
  totalPages,
  children,
  isPageable,
  noDataMessage = `Не удалось загрузить данные`
}) => {
  const [isFirstPageLoaded, setFirstPageStatus] = useState(isLoaded && isPageable)
  
  const isDataRenderRequired = (isLoaded && isDataExists) ||
    (!Number.isNaN(+totalPages) && +totalPages > +currentPage)
  
  useEffect(() => {
    if (isPageable && isLoaded) {
      setFirstPageStatus(true)
    }
  }, [isPageable, isLoaded])
  
  if (isFirstPageLoaded) return children
  
  if (isLoading) {
    return <Spinner />
  }

  if (isDataRenderRequired) {
    return typeof children === `function` ? children() : children
  }
  
  if (isLoaded && !isDataExists) {
    return (
      <NoDataMessage>
        {noDataMessage}
      </NoDataMessage>
    )
  }

  return null
}

export default PreloaderManager
