import produce from 'immer'
import { LoadingStatuses } from 'utils/const'
import { SAVE_BLOG_POSTS, SET_BLOG_POSTS_FETCH_STATUS } from 'store/actions/actionTypes'

const INITIAL_STATE = {
  data: null,
  status: LoadingStatuses.IDLE
}

export const blogReducer = produce((draft = INITIAL_STATE, {type, payload}) => {
  switch (type) {
    case SAVE_BLOG_POSTS: {
      draft.data = payload
      return draft
    }
    case SET_BLOG_POSTS_FETCH_STATUS: {
      draft.status = payload
      return draft
    }
    default:
      return draft
  }
})