import { memo, useCallback } from 'react'
import css from './CreatePost.module.scss'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { BLOG } from 'utils/routes'
import { FormItemTypes } from 'utils/const'
import Form from 'components/Organisms/Form/Form'
import { useSelector } from 'react-redux'
import { saveImage, savePost } from 'api/blog'
import { stateToHTML } from 'draft-js-export-html'
import { useHistory } from 'react-router-dom/cjs/react-router-dom'

function CreatePost ({match, isEdit}) {
  const FORM_NAMESPACE = `create-post`
  const history = useHistory()
  const db = useSelector(store => store.firebase.app)
  const postData = history.location.state
  const postID = match.params?.id
  const isEditing = Boolean(postID)
  
  const headingLabel = isEditing
    ? `Редактировать запись`
    : `Создать запись`
  
  const backlinkURL = isEditing
    ? `${BLOG}/${postData?.id}`
    : BLOG
  
  const {
    id,
    content: postContent,
    image: defaultImage,
    heading,
    preview
  } = postData || {}
  
  const formDescription = [
    {
      element: FormItemTypes.INPUT,
      name: `${FORM_NAMESPACE}-heading`,
      defaultValue: heading,
      validation: {
        required: true
      },
      placeholder: `Введите заголовок поста`,
      className: css.block
    },
    {
      element: FormItemTypes.INPUT,
      name: `${FORM_NAMESPACE}-preview`,
      defaultValue: preview,
      validation: {
        required: true
      },
      placeholder: `Введите краткое описание темы поста`,
      className: css.block
    },
    {
      element: FormItemTypes.EDITOR,
      name: `${FORM_NAMESPACE}-content`,
      defaultValue: postContent,
      validation: {
        required: true
      },
      className: css.block
    },
    {
      element: FormItemTypes.IMAGE,
      callbackOnChange: () => {},
      defaultValue: [],
      label: `Прикрепить файл`,
      name: `${FORM_NAMESPACE}-image`,
      isMultiple: false,
      className: css.imageInput,
      validation: {
        required: true
      }
    }
  ]
  
  const submitForm = useCallback(data => {
    const {
      [`${FORM_NAMESPACE}-heading`]: heading,
      [`${FORM_NAMESPACE}-content`]: content,
      [`${FORM_NAMESPACE}-image`]: image,
      [`${FORM_NAMESPACE}-preview`]: preview,
    } = data
    
    const dataToSubmit = {
      heading,
      preview,
      content: stateToHTML(content.getCurrentContent()),
      createdAt: Date.now(),
      image: image[0].name
    }
    
    console.log(isEdit)
    
    if (isEdit) {
      alert(`Edit post`)
    } else {
      savePost({db, data: dataToSubmit})
        .then(postID => {
          return saveImage({postID, image: image[0]})
        })
        .then(response => {
        })
      }
  }, [db, isEdit])
  
  return (
    <section className={css.section}>
      <Container>
        <div className={css.header}>
          <Heading>
            { headingLabel }
          </Heading>
          <Button
            variant={ButtonVariants.ARROW_REVERSE}
            link={backlinkURL}
          >
            Назад
          </Button>
        </div>
        <Form
          submitForm={submitForm}
          data={formDescription}
        >
          {({items}) => (
            <>
              { items }
              <div className={css.footer}>
                <Button
                  variant={ButtonVariants.FILLED}
                  type="submit"
                >
                  Поделиться
                </Button>
              </div>
            </>
          )}
        </Form>
      </Container>
    </section>
  )
}

export default memo(CreatePost)