import { fetchPosts } from 'api/blog'
import { SAVE_BLOG_POSTS, SET_BLOG_POSTS_FETCH_STATUS } from 'store/actions/actionTypes'
import { LoadingStatuses } from 'utils/const'

export function setPostsFetchingStatus (status) {
  return {
    type: SET_BLOG_POSTS_FETCH_STATUS,
    payload: status
  }
}

export function getBlogPosts () {
  return function (dispatch, getState) {
    const db = getState().firebase.app
    
    dispatch(setPostsFetchingStatus(LoadingStatuses.LOADING))
    
    fetchPosts(db)
      .then(data => {
        console.log(data)
        
        dispatch({
          type: SAVE_BLOG_POSTS,
          payload: data
        })
      })
      .then(() => {
        dispatch(setPostsFetchingStatus(LoadingStatuses.LOADED))
      })
      .catch(() => {
        dispatch(setPostsFetchingStatus(LoadingStatuses.ERROR))
      })
  }
}