import { memo } from 'react'
import css from './ProjectCard.module.scss'
import classnames from 'classnames'
import Heading, {HeadingTypes} from 'components/Atoms/Heading/Heading'
import IconTimeline from 'assets/icons/IconTimeline'
import IconLocationFancy from 'assets/icons/IconLocationFancy'

function ProjectCard ({
  className,
  image,
  name,
  country,
  region,
  duration,
  type,
  customer,
  description,
  isOdd,
  TagName = `li`
}) {
  return (
    <TagName
      className={classnames(css.wrapper, className, {
        [css.wrapperOdd]: isOdd
      })}
    >
      {image && (
        <img
          className={css.img}
          src={image}
          alt={name}
        />
      )}
      <div className={css.description}>
        <Heading
          tag={HeadingTypes.H4}
          style={HeadingTypes.H4}
          isLightest={isOdd}
        >
          { name }
        </Heading>
        <ul className={css.details}>
          <li className={css.detail}>
            <IconLocationFancy className={classnames(css.icon, css.iconLocation)} />
            { country }
          </li>
          {region && (
            <li className={css.detail}>
              <IconLocationFancy className={classnames(css.icon, css.iconLocation)}/>
              {region}
            </li>)
          }
          <li className={css.detail}>
            <IconTimeline className={classnames(css.icon, css.iconTimeline)} />
            { `${duration.start}-${duration.end}` }
          </li>
        </ul>
        <dl className={css.list}>
          <div className={css.item}>
            <dt className={css.key}>
              Вид работ
            </dt>
            <dd className={css.value}>
              { type }
            </dd>
          </div>
          {customer && (
            <div className={css.item}>
              <dt className={css.key}>
                Заказчик
              </dt>
              <dd className={css.value}>
                {customer}
              </dd>
            </div>
          )}
        </dl>
        <div className={css.wyziwig} dangerouslySetInnerHTML={{ __html: description }} />
      </div>
    </TagName>
  )
}

export default memo(ProjectCard)