import css from './Contacts.module.scss'
import CompanyLocation from 'components/Atoms/CompanyLocation/CompanyLocation'
import Heading from 'components/Atoms/Heading/Heading'
import { Fragment } from 'react'

function Contacts ({
  info,
  map,
}) {
  const {
    heading,
    location,
    phones,
    email
  } = info

  function renderContactItem ({IconComponent, list}) {
    return (
      <li className={css.item}>
        <IconComponent className={css.icon} />
        <ul className={css.sublist}>
          { list.map((item, index) => (
            <li className={css.subitem} key={index}>
              { item }
            </li>
          )) }
        </ul>
      </li>
    )
  }

  function renderItems () {
    const {
      IconComponent: IconLocation,
      label: labelLocation
    } = location
    
    const {
      IconComponent: IconPhones,
      labels: labelsPhones
    } = phones
    
    const {
      IconComponent: IconEmail,
      labels: labelsEmail
    } = email
    
    return (
      <>
        <li className={css.item}>
          <IconLocation className={css.icon} />
          { labelLocation }
        </li>
        <li className={css.item}>
          <IconPhones className={css.icon} />
          <ul className={css.sublist}>
            { labelsPhones.map((item, index) => (
              <li className={css.subitem} key={index}>
                <a href={`tel:${item}`}>
                  { item }
                </a>
              </li>
            )) }
          </ul>
        </li>
        <li className={css.item}>
          <IconEmail className={css.icon} />
          <ul className={css.sublist}>
            { labelsEmail.map((item, index) => (
              <li className={css.subitem} key={index}>
                <a href={`mailto:${item}`}>
                  { item }
                </a>
              </li>
            )) }
          </ul>
        </li>
      </>
    )
    // return list
    //   .map((item, index) => (
    //     <Fragment key={index}>
    //       { renderContactItem({...item}) }
    //     </Fragment>
    //   ))
  }

  return (
    <section className={css.wrapper}>
      <CompanyLocation className={css.map} mapState={map} />
      <div className={css.contacts}>
        <Heading isCapitalized>
          { heading }
        </Heading>
        <ul className={css.list}>
          { renderItems() }
        </ul>
      </div>
    </section>
  )
}

export default Contacts
