import css from './Projects.module.scss'
import ProjectsSlider from 'components/Molecules/ProjectsSlider/ProjectsSlider'
import ProjectsDataProvider from 'components/Organisms/ProjectsDataProvider/ProjectsDataProvider'
import ProjectsList from 'components/Molecules/ProjectsList/ProjectsList'
import ProjectsFilter from 'components/Molecules/ProjectsFilter/ProjectsFilter'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'

function Projects () {
  return (
    <div className={css.wrapper}>
      <ProjectsSlider />
      <ProjectsDataProvider>
        {
          ({data, ...props}) => (
            <Container className={css.container}>
              <div className={css.intro}>
                <Heading>
                  Наши наиболее значимые проекты
                </Heading>
                <ProjectsFilter {...props} />
              </div>
              <ProjectsList
                data={data}
                className={css.list}
              />
            </Container>
          )
        }
      </ProjectsDataProvider>
    </div>
  )
}

export default Projects
