import css from './SlideLogo.module.scss'
import classnames from 'classnames'

function SlideLogo ({
  className,
  logo,
  label
}) {
  return (
    <div className={classnames(css.wrapper, className)}>
      <img
        src={logo}
        className={css.logo}
        alt={label}
      />
    </div>
  )
}

export default SlideLogo
