import { useSelector } from 'react-redux'
import { useEffect, useState } from 'react'
import { fetchSpecificPost } from 'api/blog'
import { getDateComponents, getFormattedDate } from 'utils'
import { LoadingStatuses } from 'utils/const'

function PostDataProvider ({match, location, renderChildren}) {
  const isAuth = useSelector(store => store.auth.email)
  const db = useSelector(store => store.firebase.app)
  const postID = match.params.id
  const routerData = location.state?.data
  const [data, setData] = useState(location.state?.data)
  const [status, setStatus] = useState(routerData ? LoadingStatuses.LOADED : LoadingStatuses.IDLE)
  
  useEffect(() => {
    if (!data && status !== LoadingStatuses.LOADING) {
      setStatus(LoadingStatuses.LOADING)
      fetchSpecificPost({
        postID,
        db
      })
        .then(postData => {
          setData(postData)
          setStatus(LoadingStatuses.LOADED)
        })
        .catch(() => {
          setStatus(LoadingStatuses.ERROR)
        })
    }
  }, [data, postID, db, status])
  
  return renderChildren({
    isAuth,
    dateString: getFormattedDate(data?.createdAt),
    fetchingStatus: status,
    ...data
  })
}

export default PostDataProvider