import { useDispatch, useSelector } from 'react-redux'
import { useEffect } from 'react'
import { LoadingStatuses } from 'utils/const'
import { getBlogPosts } from 'store/actions'

function BlogDataProvider ({renderChildren}) {
  const blogState = useSelector(store => store.blog)
  const dispatch = useDispatch()
  console.log(blogState)
  
  const {
    data,
    status
  } = blogState
  
  useEffect(() => {
    if (!data && status === LoadingStatuses.IDLE) {
      dispatch(getBlogPosts())
    }
  }, [data, status])
  
  return renderChildren({
    posts: data,
    fetchingStatus: status
  })
}

export default BlogDataProvider